
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { validateNotNull, validateInt1 } from 'util/rules'
export default {
data () {
return {
show1: false,
show2: false,
newDate: '',
buyProduct: '',
buyProduct2: '',
nutrition_type: [],
nutrition_type_all: {},
nutrition: {
buyDate: '',
buyNum: '',
buyProduct: '',
clientInfoId: 0,
createBy: '',
createTime: '',
id: null,
tillDate: '',
type: '5',
updateBy: '',
updateTime: ''
},
inited: false
}
},
methods: {
saveService () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
let data = {
clientInfoId: this.nutrition.clientInfoId,
type: 5,
nutrition: this.nutrition
}
if (validateNotNull(this, this.nutrition.buyDate, '购买日期') && validateNotNull(this, this.nutrition.buyProduct, '购买产品') && validateInt1(this, this.nutrition.buyNum, '购买数量')) {
this.inited = true
this.$tupHttp(this, 'client-biz/client-biz/storeClient/saveService', 'POST', data).then(res => {
this.$vux.toast.text('保存成功！')
setTimeout(() => {
this.$router.push({
path: '/customerSRV',
query: {
id: this.$route.query.id,
type: 5
}
})
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
},
getBuyProduct () {
this.show2 = false
this.buyProduct2 = this.buyProduct
this.nutrition_type_all.forEach(item => {
if (this.buyProduct === item.dictName) {
this.nutrition.buyProduct = item.dictValue
}
})
},
getBuyDate () {
this.show1 = false
this.nutrition.buyDate = this.newDate
},
_getDicts () {
let data = ['nutrition_type']
this.$tupHttp(this, 'front/dict/getDicts', 'POST', data).then(res => {
this.nutrition_type_all = res.data.model.nutrition_type
let sItem = []
res.data.model.nutrition_type.map((item, index) => {
sItem.push(item.dictName)
})
this.nutrition_type = sItem
})
},
getTime () {
var _this = this
let yy = new Date().getFullYear()
let mm = new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1
let dd = new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()
_this.newDate = yy + '-' + mm + '-' + dd
}
},
created () {
this.nutrition.clientInfoId = parseInt(this.$route.query.id)
this._getDicts()
this.getTime()
},
components: {
XHeader,
PopupHeader,
Popup,
Group,
Radio,
Datetime
}
}



